    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.rps-button-container {
    background-color: var(--background-colour);
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

[class*="-sign-button"] { position: relative; }

