    background-color: var(--cursor);
    position: absolute;
    top: 0;
    left: 0;
    animation: blink 1.5s infinite;
    animation-fill-mode: both;
}

.information-container { font-size: 1.5rem; }

.info-message { font-size: 1rem; }

